.exp-wrapper__block {
  padding-right: 40px;
}
// ==============
.wrapper {
    position: relative;
    top: 0;
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    max-height: 100%;
    // background: deepskyblue;
    overflow: hidden;
  }
  
  .panel {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    
    &._active {
      z-index: 10;
    }
  }
  
  .trans-layer, .section {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
  } 
  
  .trans-layer {
    // background: #000;
    transform: translateY(-100%) skew(0) rotate(0) scale(2);
    border-radius: 50%;
    transition: all 500ms ease-in-out;
    transition-delay: 500ms;
    ._active &{
      transform: translateY(0) skew(0) rotate(0) scale(2);
      transition-delay: 0ms;
    }
    
  }
  
  .section {
    transform: translateY(-100px);
    transition: all 500ms ease-in-out;
    transition-delay: 0ms;
    // background: #343434;
      ._active &{
      transform: translateY(0);
      transition-delay: 500ms;  
    }
  }
  
  .section-content {
    display: flex;
    flex-flow: column;
    width: 90%;
    height: 100%;
    justify-content: center;
    align-items: center;
    color: #fff;
    opacity: 0;
    transform: translateY(30px);
    transition: all 500ms ease;
    transition-delay: 0s;
    
    ._active & {
      opacity: 1;
      transform: translateY(0px);
      transition-delay: 1000ms;
    }
  }
  
  // Navigation
  .nav-dots {
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    z-index: 20;
    width: 40px;
    
    &:before {
      content: '';
      width: 1px;
      height: 120px;
      background: white;
      position: absolute;
      top: -100%;
      left: 15px;
    }
  }
  
  .nav-dot {
    // width: 10px;
    // height: 10px;
    // margin: 20px auto;
    // border-radius: 50%;
    // background: #fff;
    cursor: pointer;
    transition: all 300ms ease-out;
    color: white;

    font-family: "SourceSerif-Bold";
    
    &.active {
      // background: red;  

      p{
        color: $color-main;
        font-size: 20px;
        
      }
    }
    
  }
  
  
  .next-btn, .prev-btn {
    width: 30px;
    height: 30px;  
    position: absolute;
    z-index: 20;
    border: 1px solid #fff;
    color: #fff;
    line-height: 30px;
    text-align: center;
    font-size: 24px;
    cursor: pointer;
  }
  
  .next-btn {
    bottom: 10px;
    right: 14px;
  }
  
  .prev-btn {
    top: 10px;
    right: 14px;
  }